function readNIFTI(name, data) {
	var canvas = document.getElementById('myCanvas');
	var slider = document.getElementById('myRange');
	var niftiHeader, niftiImage;

	// parse nifti
	if (nifti.isCompressed(data)) {
		data = nifti.decompress(data);
	}

	if (nifti.isNIFTI(data)) {
		niftiHeader = nifti.readHeader(data);
		niftiImage = nifti.readImage(niftiHeader, data);
	}

	// set up slider
	var slices = niftiHeader.dims[3];
	slider.max = slices - 1;
	slider.value = Math.round(slices / 2);
	slider.oninput = function() {
		drawCanvas(canvas, slider.value, niftiHeader, niftiImage);
	};

	// draw slice
	drawCanvas(canvas, slider.value, niftiHeader, niftiImage);
}

function drawCanvas(canvas, slice, niftiHeader, niftiImage) {
	// get nifti dimensions
	var cols = niftiHeader.dims[1];
	var rows = niftiHeader.dims[2];

	// set canvas dimensions to nifti slice dimensions
	canvas.width = cols;
	canvas.height = rows;

	// make canvas image data
	var ctx = canvas.getContext("2d");
	var canvasImageData = ctx.createImageData(canvas.width, canvas.height);

	// convert raw data to typed array based on nifti datatype
	var typedData;

	if (niftiHeader.datatypeCode === nifti.NIFTI1.TYPE_UINT8) {
		typedData = new Uint8Array(niftiImage);
	} else if (niftiHeader.datatypeCode === nifti.NIFTI1.TYPE_INT16) {
		typedData = new Int16Array(niftiImage);
	} else if (niftiHeader.datatypeCode === nifti.NIFTI1.TYPE_INT32) {
		typedData = new Int32Array(niftiImage);
	} else if (niftiHeader.datatypeCode === nifti.NIFTI1.TYPE_FLOAT32) {
		typedData = new Float32Array(niftiImage);
	} else if (niftiHeader.datatypeCode === nifti.NIFTI1.TYPE_FLOAT64) {
		typedData = new Float64Array(niftiImage);
	} else if (niftiHeader.datatypeCode === nifti.NIFTI1.TYPE_INT8) {
		typedData = new Int8Array(niftiImage);
	} else if (niftiHeader.datatypeCode === nifti.NIFTI1.TYPE_UINT16) {
		typedData = new Uint16Array(niftiImage);
	} else if (niftiHeader.datatypeCode === nifti.NIFTI1.TYPE_UINT32) {
		typedData = new Uint32Array(niftiImage);
	} else {
		return;
	}
	
	// offset to specified slice
	var sliceSize = cols * rows;
	var sliceOffset = sliceSize * slice;

	// draw pixels
	for (var row = 0; row < rows; row++) {
		var rowOffset = row * cols ;

		for (var col = 0; col < cols; col++) {
			var offset = sliceOffset + rowOffset + col;
			var value = typedData[offset];
			/* 
			   Assumes data is 8-bit, otherwise you would need to first convert 
			   to 0-255 range based on datatype range, data range (iterate through
			   data to find), or display range (cal_min/max).
			   
			   Other things to take into consideration:
				 - data scale: scl_slope and scl_inter, apply to raw value before 
				   applying display range
				 - orientation: displays in raw orientation, see nifti orientation 
				   info for how to orient data
				 - assumes voxel shape (pixDims) is isometric, if not, you'll need 
				   to apply transform to the canvas
				 - byte order: see littleEndian flag
			*/
			canvasImageData.data[(rowOffset + col) * 4] = value & 0xFF;
			canvasImageData.data[(rowOffset + col) * 4 + 1] = value & 0xFF;
			canvasImageData.data[(rowOffset + col) * 4 + 2] = value & 0xFF;
			canvasImageData.data[(rowOffset + col) * 4 + 3] = 0xFF;
		}
	}

	ctx.putImageData(canvasImageData, 0, 0);
}

function makeSlice(file, start, length) {
	var fileType = (typeof File);

	if (fileType === 'undefined') {
		return function () {};
	}

	if (File.prototype.slice) {
		return file.slice(start, start + length);
	}

	if (File.prototype.mozSlice) {
		return file.mozSlice(start, length);
	}

	if (File.prototype.webkitSlice) {
		return file.webkitSlice(start, length);
	}

	return null;
}

function readFile(file) {
	console.log("blob:",file)
	var blob = makeSlice(file, 0, file.size);
	var reader = new FileReader();

	reader.onloadend = function (evt) {
		if (evt.target.readyState === FileReader.DONE) {
			readNIFTI(file.name, evt.target.result);
		}
	};

	reader.readAsArrayBuffer(blob);
}





// const iframe = '<iframe  id= "myframe1" frameBorder="0" width="500px" height="500px" src={process.env.PUBLIC_URL + "/3D.html"} ></iframe>';

class Test extends Component {

	constructor(props){ //构造函数
		super(props);
		this.state = {
		filename : '',
		isFetching: false,
		FormData,
		show3D:false,
		}
		}

	onChange(e) {
		var form = new FormData();

		for (var i = 0; i < 4; i++) {
			const file = e.target.files[i];
			if (file){
				form.append('filessss',file,file.name)
			}
		}
		this.setState({
			FormData:form
		})
		// console.warn("file:",this.state.FormData.file)
	}

	getData(e){ //请求数据函数
		e.preventDefault();
		this.setState({isFetching:false,
	});
	let formData = this.state.FormData;

		fetch(`http://127.0.0.1:3000/filesname`,{
		method: 'POST',
		body: formData
		})
		.then(response=> 
			response.blob()
		)
		.then( blob=> {
			readFile(blob);
		})

		}
	

	onClick(e) {
		e.preventDefault();
		this.getData(e)
		console.log("success")
	}
	
	show3D(e) {
		e.preventDefault();
		this.setState({
			show3D:!this.state.show3D,
		})
	}
	// iframe() {
	// 	return {
	// 	  __html: this.props.iframe
	// 	}
	//   }

	render() {
		const isFetching = this.state.isFetching;
		// const res_img = this.state.res_img;
		return(
			<div>
			<hr>
			</hr>

			<form encType="multipart/form-data" target="myIframe" className='myform'>
				<input type="file"  name='file' multiple = "multiple" onChange={(e)=>this.onChange(e)}/>
				<input type="submit" value="提交文件" onClick={this.onClick.bind(this)} />
			</form>
			
			
			<div id="results" className="mycanvas" align='center'>
			<h4>2D切片</h4>
				<canvas  id="myCanvas" align='center' width="200" height="200"></canvas><br />
				<input type="range" min="1" max="100"  className="slider" id="myRange" />
			</div>
			<div className = "myframe">
			<h4>3D重建</h4><span>
			<input type='submit' onClick={this.show3D.bind(this)} value='点击查看3维还原'></input>
			</span>
			{this.state.show3D ? <iframe id= "myframe1" frameBorder="0" width="500px" height="500px" src={process.env.PUBLIC_URL + '/3D.html'} ></iframe> :null}
			
			</div>
		</div>

		)
	}
}

export default Test

